@use "../abstracts" as abs;
.root-container {
  background: {
    image: url(../images/mobile/image-hero.jpg);
    repeat: no-repeat;
    size: cover;
    position: center;
  }

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 650px;

  @include abs.media-query(abs.$screen-sm) {
    background-image: url(../images/desktop/image-hero.jpg);
  }
}
